import React from "react";
import {Button, Form, Input} from "antd";
import IntlMessages from "util/IntlMessages";
import { FormattedMessage } from "react-intl";

const FormItem = Form.Item;


const ForgotPassword = () => {

  const onFinishFailed = errorInfo => {
    console.log('Failed:', errorInfo);
  };

  const onFinish = values => {
    console.log("Received values of form",values)
  };

  return (
    <div className="gx-login-container">
      <div className="gx-login-content">

        <div dir="rtl" className="gx-mb-4">
          <h2><IntlMessages id="app.userAuth.forgotPassword"/> ?</h2>
        </div>

        <Form
          initialValues={{ remember: true }}
          name="basic"
          onFinish={onFinish}
          onFinishFailed={onFinishFailed}
          className="gx-signin-form gx-form-row0">
            
          <FormItem name="email" rules={[{ required: true, message: <IntlMessages id="forgot.PhoneRequired"/> }]}>

              <FormattedMessage id="columns.mobile" defaultMessage="name">
              {
                placeholder => (
                  <Input
                  className='gx-input-lineheight'
                  type='email'
                  required
                  placeholder={placeholder}
                  margin="none"/>
                  )
                }
              </FormattedMessage>
          </FormItem>

          <FormItem>
            <Button type="primary" htmlType="submit">
              <IntlMessages id="app.userAuth.send"/>
            </Button>
          </FormItem>
          
        </Form>

      </div>
    </div>
  );
}

export default ForgotPassword
